body
  background-color: black
  
.Clock
  display: block
  color: white
  background-color: black
  position: absolute
  top: 60% !important
  left: 50% !important
  width : unset !important
  height : unset !important
  transform: translate(-50%, -50%)
  padding-left: 17px
  padding-right: 17px
  
.Pic
  position: absolute
  top: 0
  left: 0
  width: 100%
  height: 100%
  cursor: grab
  &__close
    position: absolute
    top: 20px
    right: 25px
    width: 25px
    height: 25px
    cursor: pointer
.d-none
  display: none !important


a.visited
  text-decoration: line-through

draggable img
  pointer-events: none !important